import React, {useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {inputSlice} from "../../store/reduces/slice";
import TextArea from "antd/es/input/TextArea";
import {Button, Modal} from "antd";
const InputCustom = () => {
    const dispatch = useDispatch()
    const {exampleFigure} = useSelector(state => state.inputReducer)
    const {setArrayForFigure} = inputSlice.actions
    const [state, setState] = useState(exampleFigure)

    let regexp = /[a-z-]/i;
    const setArrButton = (e) => {
        e.preventDefault()
        try {
            JSON.parse(`${state}`)
            dispatch(setArrayForFigure(state))
        } catch {
            const error = () => {
                Modal.error({
                    title: '!',
                    content: '   .',
                });
            };
            error()
        }
    }
    return (
        <form>
            <TextArea
                value={state}
                onChange={e => {
                    if (!regexp.test(e.target.value)) setState(e.target.value)
                }}
                type='text'
                placeholder="  "
                autoSize={{
                    minRows: 2,
                    maxRows: 6,
                }}
            />
            <Button style={{top: "20px"}} type="primary" onClick={setArrButton}>   </Button>
        </form>
    );
};
export default InputCustom;